<template>
  <div v-if="orders" class="orders">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
    <el-input
      style="width: 40%; margin: 16px 0"
      placeholder=""
      @clear="searchOrder"
      v-model="query.query"
      clearable
    >
      <el-button
        @click="searchOrder"
        slot="append"
        icon="el-icon-search"
      ></el-button>
    </el-input>
    <el-table :data="orders.goods" border style="width: 100%">
      <el-table-column label="#" width="60"> </el-table-column>
      <el-table-column
        prop="order_number"
        label=""
        width="240"
      ></el-table-column>
      <el-table-column
        prop="order_price"
        label=""
        width="100"
      ></el-table-column>
      <el-table-column prop="pay_status" label="" width="100">
        <template v-slot="{ row }">
          <el-tag
            size="small"
            effect="dark"
            :type="row.pay_status === '0' ? 'danger' : ''"
            >{{ row.pay_status === "0" ? "" : "" }}</el-tag
          >
        </template>
      </el-table-column>

      <el-table-column
        prop="is_send"
        label=""
        width="100"
      ></el-table-column>
      <el-table-column prop="create_time" label="" width="200">
        <template v-slot="{ row }">
          {{ row.create_time | moment("YYYY-MM-DD HH:MM:SS") }}
        </template>
      </el-table-column>
      <el-table-column label="" width="180">
        <template v-slot="{ row }">
          <el-button
            @click="editOrderDia(row)"
            type="primary"
            size="small"
            icon="el-icon-edit"
          ></el-button>
          <el-button
            @click="lookWuliu(row.user_id)"
            type="success"
            size="small"
            icon="el-icon-location"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="sizeChange"
      @current-change="currentChange"
      :page-sizes="[10, 15, 20, 25]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="orders.total"
    >
    </el-pagination>
    </el-card>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="form" :rules="rules">
        <el-form-item label="/" prop="city">
          <el-cascader
            :options="cityOptions"
            v-model="cityOptions.value"
            @change="changeProvince"
          >
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="detail">
          <el-input v-model="form.detail" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="dialogFormVisible = false"
          > </el-button
        >
      </div>
    </el-dialog>
    <Kuaidi />
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Kuaidi from "../components/Kuaidi.vue";
import cityOptions from "../../provinces-china/city_data2017_element";
export default {
  components: { Kuaidi },
  data() {
    return {
      dialogFormVisible: false,

      form: {
        city: "",
        detail: "",
      },
      rules: {
        city: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        detail: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
      query: {
        query: "",
        pagesize: 10,
        pagenum: 1,
      },
      cityOptions: cityOptions,
    };
  },
  computed: {
    ...mapState({
      orders: (state) => state.orders.orders,
    }),
  },
  methods: {
    ...mapActions(["getOrders", "getwuliu"]),
    // 
    sizeChange(val) {
      this.query.pagesize = val;
      this.getOrders(this.query);
    },
    // 
    currentChange(val) {
      this.query.pagenum = val;
      this.getOrders(this.query);
    },
    searchOrder() {
      this.getOrders(this.query);
    },
    editOrderDia(row) {
      this.dialogFormVisible = true;
      this.form = { ...row };
    },

    lookWuliu(id) {
      this.getwuliu(id);
    },
    changeProvince(value) {
      console.log(value);
    },
  },
  created() {
    this.getOrders(this.query);
    console.log(this.cityOptions);
  },
};
</script>

